// src/component/userService/FriendManagerModal.tsx
import React, { useEffect, useState } from "react";
import "@/styles/blogService/moduleDeleteConfirmModal.css";
import {
  sendFriendRequest,
  acceptFriendRequest,
  rejectFriendRequest,
  deleteFriend,
} from "@/api/userService/friends";

type TabType = "add" | "requests" | "friends";

interface FriendItem {
  friendId: number;
  userSignId: string;
}

export default function FriendManagerModal({
                                             currentUserSignId,
                                             isOpen,
                                             onClose,
                                           }: {
  currentUserSignId: string;
  isOpen: boolean;
  onClose: () => void;
}) {
  const [tab, setTab] = useState<TabType>("add");
  const [inputId, setInputId] = useState("");
  const [requests, setRequests] = useState<FriendItem[]>([]);
  const [friends, setFriends] = useState<FriendItem[]>([]);
  const [loading, setLoading] = useState(false);

  const API_BASE = process.env.NEXT_PUBLIC_USERSERVICE_API;

  const loadData = async () => {
    setLoading(true);
    try {
      const [reqRes, friendRes] = await Promise.all([
        fetch(`${API_BASE}/friends/requests/received`, {
          headers: { userSignId: currentUserSignId },
        }),
        fetch(`${API_BASE}/friends`, {
          headers: { userSignId: currentUserSignId },
        }),
      ]);

      if (reqRes.ok) {
        const data = await reqRes.json();
        setRequests(data.map((d: any) => ({ friendId: d.friendId, userSignId: d.requesterSignId })));
      }
      if (friendRes.ok) {
        const data = await friendRes.json();
        setFriends(
            data.map((d: any) => ({
              friendId: d.friendId,
              userSignId: d.requesterSignId === currentUserSignId ? d.receiverSignId : d.requesterSignId,
            }))
        );
      }
    } catch (err) {
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (isOpen) loadData();
  }, [isOpen]);

  const sendRequest = async () => {
    if (!inputId.trim()) return alert("ì•„ì´ë””ë¥¼ ì…ë ¥í•´ì£¼ì„¸ìš”");
    try {
      await sendFriendRequest(currentUserSignId, inputId.trim());
      alert("ì¹œêµ¬ ìš”ì²­ì„ ë³´ëƒˆìŠµë‹ˆë‹¤!");
      setInputId("");
    } catch (err: any) {
      alert(err.message || "ìš”ì²­ ì‹¤íŒ¨");
    }
  };

  const accept = async (friendId: number) => {
    await acceptFriendRequest(currentUserSignId, friendId);
    loadData();
  };

  const reject = async (friendId: number) => {
    await rejectFriendRequest(currentUserSignId, friendId);
    loadData();
  };

  const remove = async (friendId: number) => {
    if (!confirm("ì •ë§ ì‚­ì œí•˜ì‹œê² ìŠµë‹ˆê¹Œ?")) return;
    await deleteFriend(currentUserSignId, friendId);
    loadData();
  };

  if (!isOpen) return null;

  return (
      <div className="modalBackdrop" style={{ zIndex: 9999 }} onClick={onClose}>
        <div
            className="modalContent"
            style={{ maxWidth: "440px", width: "95%" }}
            onClick={(e) => e.stopPropagation()}
        >
          <h2 className="modalTitle">ì¹œêµ¬ ê´€ë¦¬</h2>

          {/* íƒ­ */}
          <div className="flex border-b mb-5 -mx-8">
            {(["add", "requests", "friends"] as TabType[]).map((t) => (
                <button
                    key={t}
                    onClick={() => setTab(t)}
                    className={`flex-1 py-3 text-sm font-semibold transition ${
                        tab === t
                            ? "text-red-600 border-b-2 border-red-600"
                            : "text-gray-500 hover:text-gray-700"
                    }`}
                >
                  {t === "add" && "ì¹œêµ¬ ì¶”ê°€"}
                  {t === "requests" && `ë°›ì€ ìš”ì²­ (${requests.length})`}
                  {t === "friends" && `ë‚´ ì¹œêµ¬ (${friends.length})`}
                </button>
            ))}
          </div>

          {/* íƒ­ ë‚´ìš© */}
          <div className="min-h-64">
            {/* ì¹œêµ¬ ì¶”ê°€ */}
            {tab === "add" && (
                <div className="space-y-6">
                  <input
                      type="text"
                      value={inputId}
                      onChange={(e) => setInputId(e.target.value)}
                      onKeyDown={(e) => e.key === "Enter" && sendRequest()}
                      placeholder="ì¹œêµ¬ì˜ ì•„ì´ë””ë¥¼ ì…ë ¥í•˜ì„¸ìš”"
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-red-500"
                  />
                  <p className="modalMessage text-center">
                    <strong>{inputId || "[ì•„ì´ë””]"}</strong>ë‹˜ì—ê²Œ
                    <br />
                    ì¹œêµ¬ ìš”ì²­ì„ ë³´ë‚´ì‹œê² ìŠµë‹ˆê¹Œ?
                  </p>
                  <div className="modalActions">
                    <button className="cancelButton" onClick={onClose}>
                      ì·¨ì†Œ
                    </button>
                    <button
                        className="deleteButton"
                        disabled={loading || !inputId.trim()}
                        onClick={sendRequest}
                    >
                      {loading ? "ë³´ë‚´ëŠ” ì¤‘..." : "ìš”ì²­ ë³´ë‚´ê¸°"}
                    </button>
                  </div>
                </div>
            )}

            {/* ë°›ì€ ìš”ì²­ */}
            {tab === "requests" && (
                <div className="space-y-3">
                  {requests.length === 0 ? (
                      <p className="text-center text-gray-500 py-10">ë°›ì€ ìš”ì²­ì´ ì—†ìŠµë‹ˆë‹¤</p>
                  ) : (
                      requests.map((req) => (
                          <div key={req.friendId} className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                            <span className="font-medium">{req.userSignId}</span>
                            <div className="flex gap-2">
                              <button
                                  onClick={() => reject(req.friendId)}
                                  className="px-4 py-2 text-sm bg-gray-300 rounded hover:bg-gray-400"
                              >
                                ê±°ì ˆ
                              </button>
                              <button
                                  onClick={() => accept(req.friendId)}
                                  className="px-4 py-2 text-sm bg-red-600 text-white rounded hover:bg-red-700"
                              >
                                ìˆ˜ë½
                              </button>
                            </div>
                          </div>
                      ))
                  )}
                </div>
            )}

            {/* ë‚´ ì¹œêµ¬ */}
            {tab === "friends" && (
                <div className="space-y-3">
                  {friends.length === 0 ? (
                      <p className="text-center text-gray-500 py-10">ì•„ì§ ì¹œêµ¬ê°€ ì—†ìŠµë‹ˆë‹¤</p>
                  ) : (
                      friends.map((friend) => (
                          <div key={friend.friendId} className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                            <span className="font-medium">{friend.userSignId}</span>
                            <button
                                onClick={() => remove(friend.friendId)}
                                className="text-sm text-red-600 hover:underline"
                            >
                              ì‚­ì œ
                            </button>
                          </div>
                      ))
                  )}
                </div>
            )}
          </div>

          <div className="mt-6 text-center">
            <button onClick={onClose} className="text-sm text-gray-500 hover:text-gray-700">
              ë‹«ê¸°
            </button>
          </div>
        </div>
      </div>
  );
}